<template>
  <div class="sampleComparison tapStyle secondary-page-mode">
    <div class="secondary-toolbar">
      <span>时间选择 : </span>
      <div class="layoutBox" style="width:110px">
        <el-date-picker v-model="dateRange" type="month" start-placeholder="开始日期" end-placeholder="结束日期" value-format="yyyy-MM"></el-date-picker>
      </div>&emsp;&emsp;
      <span>监测项目 : </span>
      <div class="layoutBox" style="width:190px">
            <el-select v-model="value" multiple collapse-tags placeholder="请选择">
                  <el-option
                    v-for="item in optionData"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                  </el-option>
            </el-select>
      </div>&emsp;&emsp;
      <el-button @click="search"><i class="iconfont icon-sousuo"></i>查询</el-button>
      <el-button @click="exportExcel()" type='delet'><i class="iconfont icon-daochu"></i>导出</el-button>
    </div>
    <div class="currency-mode-wrap scrollSpecial">
      <div class="reminder-wrap">
        <span class="reminder-title">温馨提示</span>
        <div class="reminder-content">
          <div class="reminder-text">
            <p>所选时间段内共有“多点线性核查移”x次，共有合格项m次，不合格项y次。影响数据n条。点位1、点位2的上次周核查时间距今已有7天；站点3的上次周核查距今已有6天。</p>
            <div><el-button type="noticonsecondBtn"> 生成任务 </el-button></div>
          </div>
        </div>
        <div class="reminder-toolbar">
          <div class="state-legend">
            <span><i class="qualified-icon"></i> 合格</span>
            <span><i class="unqualified-icon"></i> 不合格</span>
          </div>
          <div class="state-radio-wrap">
            <el-radio-group v-model="stateRadio" size="small" @change = "toggleTab">
              <el-radio-button label="1">当前断面</el-radio-button>
              <el-radio-button label="2">所有断面</el-radio-button>
            </el-radio-group>
          </div>
        </div>
      </div>
      <div class="currency-body-toggle">
        <div v-show="stateRadio === '1'" style="height: 100%">
          <div class="tablebox">
            <div class="table-head">
              <table ref="tableHead">
                <tr>
                  <th width="3%">序号</th>
                  <th width="10%">监测项目</th>
                  <th width="10%">单位</th>
                  <th width="17%">仪器测试值</th>
                  <th width="17%">人工测试值</th>
                  <th width="16%">误差</th>
                  <th width="17%">技术要求</th>
                  <th width="16%">是否合格</th>
                  <th style="width: 0px; display: none;"></th>
                </tr>
              </table>
            </div>
            <el-scrollbar style="height:calc(100% - 32px)">
              <ul class="table-body clearfix">
                <li v-for="(item,index) in tableData">
                  <div class="toggle-row" :class="{active:item.toggle}">
                    <i class="toggle-icon" @click="toggleTable(item)" :class="{active:item.toggle}"></i>
                    <span>采样时间：{{ item.time }}</span>
                  </div>
                  <el-collapse-transition>
                    <div v-if="item.toggle">
                      <table>
                        <tr v-for="(n,m) in item.list">
                          <td width="3%">{{ (m+1) }}</td>
                          <td width="10%">{{ n.indexName }}</td>
                          <td width="10%">{{ n.indexUnit }}</td>
                          <td width="17%">{{ n.automaticValue }}</td>
                          <td width="17%">{{ n.artificialValue }}</td>
                          <td width="16%">{{ n.error }}</td>
                          <td width="17%">{{ n.technicalCequirement }}</td>
                          <td width="16%">
                            <!-- <b>{{n.qualification}}</b> -->
                            <b v-if="n.qualification === '合格'">合格</b>
                            <b v-if="n.qualification === '不合格'" style="color: red">不合格</b>
                          </td>
                        </tr>
                      </table>
                    </div>
                  </el-collapse-transition>
                </li>
              </ul>
            </el-scrollbar>
          </div>
        </div>
        <div class="chart-wrap" v-show="stateRadio === '2'" style="height: 100%;">
          <div class="tablebox" style="height:calc(100% - 34px);">
            <el-table
              :data="tableDataB"
              ref="table"
              height="100%"
              border>
              <el-table-column
                type="index"
                fixed
                width="50"
                label="序号">
              </el-table-column>
              <el-table-column
                prop="areaName"
                fixed
                label="行政区">
              </el-table-column>
              <el-table-column
                prop="basinName"
                fixed
                label="流域">
              </el-table-column>
              <el-table-column
                prop="stationName"
                fixed
                label="断面名称">
              </el-table-column>
              <el-table-column
                prop="time"
                fixed
                label="采样时间">
              </el-table-column>
              <el-table-column
                v-for="(item,index) in tableHead"
                :key="index"
                :prop="item.prop"
                :label="item.label">
                <!-- <template slot-scope="scope">
                  <b v-if="parseInt(scope.row[item.prop]) === 1">合格</b>
                  <b v-if="parseInt(scope.row[item.prop]) === 0" style="color: red">不合格</b>
                </template> -->
              </el-table-column>
            </el-table>
          </div>
          <div class="paginationBox">
            <el-pagination
              @size-change="paginationSizeChange"
              @current-change="paginationChange"
              :current-page.sync="currentPage"
              :page-size="pageSize"
              background
              :small = "true"
              popper-class = "select-common-option"
              :page-sizes = "pageList"
              layout="total,sizes,slot,->,prev, pager, next, jumper"
              :total="totalRecords">
              <span class="el-pagination__current">当前第{{currentPage}}/{{totalPageNumber}}页</span>
            </el-pagination>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import {exportTableData} from '../../assets/js/tableExport'
export default {
  name: "sampleComparison",
  data() {
    return {
      areaCode:'',
      areaName:'',
      stationId:'',
      stationName:'',
      dateRange: '',
      numIndex: 0,
      value: ["w01019", "w21003", "w21011", "w21001"],
      optionData: [
        {
          value: "w01019",
          label: "高锰酸盐"
        },
        {
          value: "w21003",
          label: "氨氮"
        },
        {
          value: "w21011",
          label: "总磷"
        },
        {
          value: "w21001",
          label: "总氮"
        },
        {
          value: "w01001",
          label: "PH"
        },
        {
          value: "w01010",
          label: "水温"
        },
        {
          value: "w01009",
          label: "溶解氧"
        },
        {
          value: "w01014",
          label: "电导率"
        },
        {
          value: "w01003",
          label: "浊度"
        }
      ],
      currentPage: 1,
      pageSize: 10,
      pageList: [5, 10, 15],
      totalRecords: 1,
      tableData: [],
      tableDataB: [
        {
          area: "河南省",
          basin: "淮河流域白露河",
          nameSection: "淮滨北庙",
          samplingTime: "2019-01-25 12",
          waterTem: "1",
          ph: "1",
          dissolvedOxygen: "0",
          conductivity: "1",
          turbidity: "0",
          permanganate: "1"
        }
      ],
      tableHead:[],
      tableHeadAll: [
        {
          prop: "w01019",
          label: "高锰酸盐"
        },
        {
          prop: "w21003",
          label: "氨氮"
        },
        {
          prop: "w21011",
          label: "总磷"
        },
        {
          prop: "w21001",
          label: "总氮"
        },
        {
          label: "水温",
          prop: "w01010"
        },
        {
          label: "PH",
          prop: "w01001"
        },
        {
          label: "溶解氧",
          prop: "w01009"
        },
        {
          label: "电导率",
          prop: "w01014"
        },
        {
          label: "浊度",
          prop: "w01003"
        }
        // {
        //   label: "高锰酸盐",
        //   prop: "permanganate"
        // }
      ],
      stateRadio: "1"
    };
  },
  computed: {
    totalPageNumber() {
      //计算总页数
      let number = 0;
      number = Math.ceil(this.totalRecords / this.pageSize);
      return number;
    },
    stationInfo: {
      get: function() {
        return this.$store.state.stationInfo;
      }
    }
  },
  methods: {
    paginationChange(val) {
      //分页页码改变时执行
      this.currentPage = val;
      this.getAllTableData();
    },
    paginationSizeChange(val) {
      //分页pageSize改变时执行
      this.pageSize = val;
      this.getAllTableData();
    },
    toggleTable(item) {
      if (item.toggle) {
        item.toggle = false;
      } else {
        item.toggle = true;
      }
      console.log(item.toggle);
    },
    toggleTab(val) {
    },
    search(){
      this.tableHead = [];
      this.value.forEach((item,index)=>{
        for(let i=0; i<this.tableHeadAll.length; i++){
          if(item === this.tableHeadAll[i].prop){
            this.tableHead.push(this.tableHeadAll[i]);
            break;
          }
        }
      });
      this.getTableData();
      this.getAllTableData();
    },
    getTableData(){
      this.$http.get("/Quality/testData/realWaterSampleComparisonData", {
          params: {
            startTime: this.dateRange,
            endTime: this.dateRange,
            indexCodes: this.value.join(","),
            stationCodes: this.stationId, //this.stationId
            ifCurrentSection: true,// true为当前断面,false为所有
            // pageNumber: this.currentPage,
            // pageSize: this.pageSize
          }
      }).then(res=>{
        if(res.data.code === 0){
          console.log(res,222);
          this.tableData = res.data.content.dataList;
          this.tableData.forEach((item,index) =>{
            this.$set(item,'toggle',false);
          });
        }
      });
    },
    //实际水样比对-所有断面数据请求
    getAllTableData(){
      this.$http.get("/Quality/testData/realWaterSampleComparisonData", {
        params: {
          startTime: this.dateRange,
          endTime: this.dateRange,
          indexCodes: this.value.join(","),
          stationCodes: this.areaCode, //this.stationId
          ifCurrentSection: false,// true为当前断面,false为所有
          pageNumber: this.currentPage,
          pageSize: this.pageSize
        }
      }).then(res=>{
        if(res.data.code === 0){
          // this.tableData = res.data.content.dataList;
          this.tableDataB = res.data.content.dataList;
          this.totalRecords = res.data.content.totalRecords;
        }
      })
    },
     //导出表格
    exportExcel(){
      console.log(typeof this.stateRadio);
      let tableHead = '';
      let title = '';
      let ifCurrentSection = null;
      let stationCodes = null;
      let headerProp = '';
      if(this.stateRadio === '1'){
        tableHead = this.$refs.tableHead;
        title = this.stationName +'实际水样比对';
        ifCurrentSection = true;
        stationCodes = this.stationId;
        headerProp = 'indexName,indexUnit,automaticValue,artificialValue,error,technicalCequirement,qualification';
      }else{
        let {table} = this.$refs;
        let tableBox = table.$el;
        let str = [];
        tableHead = tableBox.querySelector('.el-table__header');
        title = this.areaName +'实际水样比对';
        ifCurrentSection = false;
        stationCodes = this.areaCode;

        this.tableHead.forEach((item,index)=>{
          str.push(item.prop);
        });
        headerProp = 'areaName,basinName,stationName,time,'+str.join(',');
      }
      let params = {
        headerProp,
        startTime: this.dateRange,
        endTime: this.dateRange,
        indexCodes: this.value.join(","),
        stationCodes,
        tableType:'REAL_WATER_DATA',
        ifCurrentSection,
        pattern:'yyyy-MM-dd HH:mm:ss'
      };
      exportTableData(tableHead,title,'','1',"/Quality/exports/qualityControlExcel",params);
    },
  },
  mounted() {

  },
  created() {
     //初始化时间
    this.dateRange = new Date();
  },
  watch: {
    stationInfo(value) {
      if (value.length > 0) {
        this.stationId = value[0].nodeId;
        this.areaCode = value[0].areaCode;
        this.areaName = value[0].areaName;
        this.stationName = value[0].nodeName;
        this.search()
      }
    }
  }
};
</script>

<style scope lang="less">
.sampleComparison {
  .tablebox {
    height: 100%;
  }
  .table-head,
  .table-body {
    table {
      width: 100%;
      border-collapse: collapse;
      td,
      th {
        border: 1px solid #d6e3f2;
        padding: 0 3px;
        height: 30px;
        text-align: center;
      }
      th{
       font-weight: 100;
      }
    }
    .toggle-row {
      height: 30px;
      line-height: 30px;
      padding-left: 6px;
      border-left: 1px solid #d6e3f2;
      border-right: 1px solid #d6e3f2;
      border-bottom: 1px solid #d6e3f2;
      &.active {
        /*border-bottom: none;*/
      }
    }
  }
  .table-body {
    table {
      margin-top: -1px;
    }
  }
  .toggle-icon {
    display: inline-block;
    vertical-align: middle;
    margin-bottom: 3px;
    width: 13px;
    height: 13px;
    background: url("../../assets/images/retract-toggle.png") no-repeat;
    cursor: pointer;
    &.active {
      background: url("../../assets/images/open-toggle.png") no-repeat;
    }
  }
}
</style>



